<!-- 显示实时的时间 -->
<template>
  <div>
    <h3>2020/10/08 周四</h3>
    <ul class="time-ul">
      <li>
        <span class="time-title">字体：cursive</span>
        <span class="time-text1">{{ msg }}</span>
      </li>

      <li>
        <span class="time-title">空心</span>
        <span class="time-text2">{{ msg }}</span>
      </li>

      <li>
        <span class="time-title">花里胡哨版本</span>
        <span class="time-text3">{{ msg }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "",
    };
  },

  props: {},

  components: {},

  created() {},

  computed: {},

  mounted() {
    this.startTime();
  },

  methods: {
    startTime() {
      let today = new Date();
      let y = today.getFullYear();
      let month = today.getMonth();
      let day = today.getDate();
      let h = today.getHours();
      let m = today.getMinutes();
      let s = today.getSeconds();

      month = Number(this.checkTime(month)) + 1;
      day = this.checkTime(day);
      m = this.checkTime(m);
      s = this.checkTime(s);

      let msg = `${y}-${month}-${day} ${h}:${m}:${s}`;
      this.msg = msg;
      let t = setTimeout(() => {
        this.startTime();
      }, 500);
    },

    checkTime(i) {
      if (i < 10) {
        i = "0" + i;
      }
      return i;
    },
  },
};
</script>
<style>
.time-title {
  margin-right: 15px;
}
.time-text1 {
  font-size: 25px;
  font-family: cursive;
  text-shadow: 2px 2px 2px #333;
  color: deepskyblue;
}
.time-text2 {
  font-size: 25px;
  color: transparent;
  -webkit-text-stroke: 1px blue;
  letter-spacing: 0.1em;
}
.time-text3 {
  font-size: 25px;
  background: #eee
    url(data:image/gif;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAIAAAAmkwkpAAAAHklEQVQImWNkYGBgYGD4//8/A5wF5SBYyAr+//8PAPOCFO0Q2zq7AAAAAElFTkSuQmCC)
    repeat;
  text-shadow: 5px -5px black, 4px -4px white;
  font-weight: bold;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}
.time-ul {
  padding: 20px;
  margin: 0 auto;
  width: 500px;
  border: 2px dotted gray;
  border-radius: 20px;
}
.time-ul > li {
  padding: 10px 0;
  text-align: left;
  border-bottom: 2px dotted gray;
}
.time-ul > li:last-child {
  border-bottom: 0;
}
</style>
