import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import About from '../views/About.vue'
import A from '../views/A'
import Lang from '../views/lang'
import Time from '../views/time'
import MultiMedia from '../views/multi-media'

Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/',
  //   name: 'Home',
  //   component: Home
  // },
  {
    path: '/',
    name: 'A',
    component: A,
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/lang',
    name: 'lang',
    component: Lang
  }, {
    path: '/time',
    name: 'time',
    component: Time
  },
  {
    path: '/top',
    name: 'top',
    component: () => import('@/views/top') //路由懒加载
  },
  {
    path:'/multiMedia',
    name:'multiMedia',
    component:MultiMedia
  }
]

const router = new VueRouter({
  routes
})

export default router
