<!-- 音视频 -->
<template>
  <div>
    <div class="main-box">
      <div class="hd-img-box">
        <img src="@/assets/images/1.jpg" />
        <p class="p1">向每个人生命中的奇幻漂流致敬</p>

        <ul class="img-desc">
          <li>
            除了太多的无能为力和意料之外，深海中的小船和人生的命运又何其相似？
          </li>
          <li>
            我们无法划清梦境与现实的界限，就像在大多数时候，最让人难过仅仅只是我们没有好好的和他们道別。
          </li>
          <li>
            在生命的旅程无尽的殊途同归中，向每个人生命中的奇幻漂流致敬。祝愿我们能够重新拾起被摧毁过的纯真，祝我们永不失去。
          </li>
        </ul>
      </div>
      <div class="player-box">
        <div>
          <div v-if="currentSong.id == 0" class="song-info">
            <img src="@/assets/images/singer/1.jpg" class="singer-img" />
            <div>
              <p>{{ currentSong.song }}</p>
              <p>{{ currentSong.name }}</p>
              <audio src="@/assets/static/nanshanyi.mp3" controls=""></audio>
            </div>
          </div>

          <div v-if="currentSong.id == 1" class="song-info">
            <img src="@/assets/images/singer/2.jpg" class="singer-img" />
            <div>
              <p>{{ currentSong.song }}</p>
              <p>{{ currentSong.name }}</p>
              <audio src="@/assets/static/huazhou.mp3" controls=""></audio>
            </div>
          </div>
        </div>
        <ol class="song-list" style="padding: 0">
          <li
            v-for="(item, index) in list"
            :key="index"
            :class="currentIndex == index ? 'active' : ''"
            @click="switchChange(index)"
          >
            <span>{{ item.song }}</span>
            <span>{{ item.name }}</span>
          </li>
        </ol>
      </div>
    </div>
    <footer>2021-01-21 by 小五</footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          id: 0,
          img: "/assets/images/singer/1.jpg",
          name: "许嵩",
          song: "南山忆",
          url: "/assets/static/nanshanyi.mp3",
        },
        {
          id: 1,
          img: "/assets/images/singer/2.jpg",
          name: "花粥",
          song: "我就静静的看着你装逼",
          url: "/assets/static/huazhou.mp3",
        },
      ],
      currentIndex: 0,
      currentSong: {},
      showImg: "",
    };
  },

  mounted() {
    this.showInfo();
  },

  methods: {
    switchChange(index) {
      this.currentIndex = index;
      this.showInfo();
    },

    showInfo() {
      this.currentSong = this.list.find((item) => {
        if (item.id == this.currentIndex) {
          this.showImg = item.img;
        }
        return item.id == this.currentIndex;
      });
    },
  },
};
</script>
<style scoped>
.main-box {
  margin: 20px auto;
  width: 90%;
}

/* 图文 */
.hd-img-box {
  margin: auto;
}
.p1 {
  padding: 10px 0;
  line-height: 1.5;
  text-decoration: underline;
  font-size: 16px;
}
.img-desc {
  margin: auto;
  width: 720px;
}
.img-desc li {
  margin: 10px 0;
  font-size: 15px;
  line-height: 1.5;
  text-align: left;
  font-family: Verdana;
}
.img-desc li:nth-child(2) {
  margin-bottom: 20px;
}

/* 播放器 */
.player-box {
  margin: 20px auto;
  padding: 20px;
  box-sizing: border-box;
  width: 500px;
  border: 1px solid lightgray;
  box-shadow: 0 2px 2px lightgray;
  border-radius: 20px;
}
.song-list li {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
  border-bottom: 1px solid lightgray;
}
.song-list li.active {
  background-color: lightgray;
  border-left: 3px solid #d94240;
}
.song-list li:last-child {
  border-bottom: 0;
}
.singer-img {
  width: 150px;
  height: 150px;
}
.song-info {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.song-info div{
    padding: 20px 0 0 20px;
    box-sizing: border-box;
    text-align: left;
}
.song-info div p:first-child{
    font-size: 18px;
    font-weight: bold;
    color:#6A6B6F;
}
.song-info div p:nth-child(2){
    font-size: 15px;
    color:#666;
}
audio {
  outline: none;
}
footer{
    padding: 50px 0;
    color: #666;
    font-size: 14px;
    text-align: center;
    text-decoration: underline;
}
</style>
